html {
    scroll-behavior: smooth;
}

.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(170, 65, 134, 0.31);
    padding-right: 1.25rem;
    gap: 1rem;
}

body {
    margin: 0;
}

.logo {
    width: 30%;
    font-family: 'Comfortaa';
    font-style: normal;
    font-weight: 400;
    font-size: 2rem;
    margin-right: 2rem;
    margin-left: 5%;
    margin-top: 1.2rem;
    display: flex;
    letter-spacing: -0.015em;
    color: #000000; 
}

li {
    text-decoration: none;
    list-style: none;
}

a {
    /*Removes underline from anchor tag in nav*/
    text-decoration: none;
    color: black;
}



.navi {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 1rem;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: -0.015em;
    color: black;
    margin-top: 1.2rem;
}

.navi:hover {
    transform: scale(1.1);
}

.welcome {
    height: 38rem;
    display: flex;
    align-items: center;
    background-color: rgba(170, 65, 134, 0.31);
}

.welcome-hr {
    border-top: 1.1px solid #A259FF;
    border-bottom: 1.1px;
    border-radius: 2px;
    width: 170px;
    margin: 0.3rem 0.2rem 0 0;
}

.leftWelcome {
    width: 40%;
    text-align: center;
}

.rightWelcome {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin-right: 2rem;
}

h4 {
    font-family: 'Montserrat';
    font-style: bold;
    font-weight: 900;
    font-size: 1.5rem;
    margin: auto;
    letter-spacing: -0.015em;
    color: #000000;
}

p {
font-family: 'Montserrat';
font-style: normal;
font-size: 1rem;
line-height: 2rem;
letter-spacing: -0.015em;
color: rgba(0, 0, 0, 0.8);
}

.welcomeImg { 
    border-radius: 50%;
    width: 75%;
    max-width: 3200px;
    margin-bottom: 15px;
}

button { 
    background: #A259FF;
    border-radius: 0.5rem;
    margin: 2% 0 5% 0;
    border: 0;
    padding: 0.5rem 1.5rem;
}

button:hover {
    transform:scale(1.05)
  }

.buttonText {
    color: #FFFFFF;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 1rem;
    letter-spacing: -0.015em;
    }

h1 {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 2rem;
    letter-spacing: -0.015em;
    color: #000000;
}

#header2 {
    margin: 9rem auto 3rem 3rem;
}

.testimonials {
    display: flex;
    justify-content: space-around;
}

.testimony-block {
    width: 50%;
    margin: 4rem;
}

.testimonyPic {
    width: 60%;
    border-radius: 50%;
    margin-bottom: 1.6rem;
}

.footer {
    height: 12rem;
    background-color: rgba(170, 65, 134, 0.31);
}

.footerList {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.fa {
    padding: 1.2rem;
    font-size: 2rem;
    width: 1.2rem;
    text-align: center;
    text-decoration: none;
    margin: 3rem 1.8rem;
    border-radius: 50%;
  }
  
  .fa:hover {
      transform: scale(1.1);
  }
  
  .fa-facebook {
    background: #A259FF;
    color: white;
  }

  .fa-instagram {
    background: #A259FF;
    color: white;
  }

  h5 {
    font-family: 'Montserrat';
    font-size: 1rem;
    font-weight: 400;
    margin-left: auto 1.6rem;
  }

/*Media Queries*/

/*Tablet*/

@media (max-width: 768px) {

    body {

    }

    .logo {
        margin-right: auto;
    }
}

/*Smartphone*/

@media (max-width: 415px) {
    body {

    }

    .logo {
        width: 100%;
        text-align: center;
        font-family: 'Comfortaa';
        font-style: bold;
        font-weight: 900;
        font-size: 1.8rem;
        margin: 1.2rem;
        display: block;
    }

    .header {
        flex-wrap: wrap;
        gap: 0;
        padding: 0;
    }

    .navi {
        width: 100%;
        text-align: center;
        display: block;
        font-size: 0.8rem;
        margin: 0.2rem;
    }

    .welcome {
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }

    .welcomeImg {
        border-radius: 50%;
        width: 11rem;
        margin-top: 1rem;
        margin-bottom: -2rem;
    }

    .leftWelcome {
        width: auto;
        text-align: center;
    }

    .rightWelcome {
        text-align: center;
        margin: 0;
    }

    .welcome-hr {
        width: 40%;
        text-align: center;
        margin: 0.5rem auto;
    }

    p {
        font-size: 0.75rem;
        line-height: 1.4rem;
    }

    #header2 {
        padding: 0;
        margin: 50px 40px 10px 40px;
    }

    h1 {
        font-size: 1.2rem;
        text-align: left;
    }

    .testimonials {
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }

    .testimony-block {
        margin: 2rem;
    }

    .testimonyPic {
        width: 9rem;
    }



    }



  